import React, { useState } from "react";
import { Scrollama, Step } from "react-scrollama";
import Timeline from './Timeline';
import Scene from "./Scene";
import data from '../data/copy.json';

function ScrollCtn(){
    const [currentStepIndex, setCurrentStepIndex] = useState(null);
    const [fullProgress, setFullProgress] = useState(0);
    // This callback fires when a Step hits the offset threshold. It receives the
    // data prop of the step, which in this demo stores the index of the step.
    let stepData = data.content.filter(t=>t.type==='step');
    let step = stepData[0].value;

    const onStepEnter = ({ data }) => {
        console.log("onStepEnter: ", data);
        setCurrentStepIndex(data);
    };

    const onStepProgress = ({ data, progress, direction }) => {
        // setCurrentStepIndex(data);
        if(data!= null){
            let full = data + (+(progress).toFixed(3));
            setFullProgress(full);
            console.log("full_progress: ", full);
        }
    };

    return (
        <div style={{ margin: "0 0", border: "2px dashed skyblue" }}>
            <div
                id="graphic_ctn_outer"
                style={{
                    position: "sticky",
                    top: 0,
                    border: "1px solid orchid",
                    height: "100vh"
                }}
            >
                <Timeline step={currentStepIndex} />
                <section id="graphic_ctn">
                    <Scene step={currentStepIndex} progress={fullProgress} />
                </section>
            </div>
            <Scrollama
                // progress
                // onStepProgress={onStepProgress}
                onStepEnter={onStepEnter}
                threshold={40}
                offset={0.5}
            >
                {step.map((_, stepIndex) => (
                    <Step data={stepIndex} key={stepIndex}>
                        <div
                            style={{
                                margin: "5vh 0",
                                padding: "40vh 1rem",
                                width: "20rem",
                                opacity:
                                    currentStepIndex === stepIndex ? 1 : 0.2
                            }}
                        >
                            <div
                                className="scroll-inner"
                                style={{
                                    padding: "0rem 1rem",
                                    background: "rgba(0, 0, 0, 0.8)",
                                    border: "1px white solid"
                                }}
                            >
                                <h4>{_["date"]}</h4>
                                <p>{_["text"]}</p>
                            </div>
                        </div>
                    </Step>
                ))}
            </Scrollama>
        </div>
    );
};

export default ScrollCtn;
