// import { Box} from "@react-three/drei";
import React from "react";
import { useThree, useFrame } from "@react-three/fiber";
import TWEEN from "@tweenjs/tween.js";
import * as THREE from "three";

let camTarget = new THREE.Vector3(0, 0, 0);
const ChangeCamera = props => {
    const { step } = props;
    // let camTarget = new THREE.Vector3(0, 0, 0);
    console.log("updateCamera: ", step);
    const { camera } = useThree();
    camera.fov = 60;
    camera.near = 0.1;
    camera.far = 1000;
    // camera.rotation.x = 1.16;
    // camera.rotation.y = -0.12;
    // camera.rotation.z = 0.27;
    camera.position.x =0;
    camera.position.y = -1.5;
    camera.position.z = 60;

    if (step === 0) {
        tweenCamera(camera, { x: 0, y: -1.5, z: 60 }, { x: 0, y: 0, z: 0 });
    } else if (step === 1) {
        // camTarget = new THREE.Vector3(0, 50, 0);
        tweenCamera(camera, { x: 0, y: -10, z: 40 }, { x: 1.16, y: -0.12, z: 0.27 });
    } else if (step === 2) {
        tweenCamera(camera, { x: 0, y: -10, z: 40 },  { x: 1.16, y: -0.12, z: 0.27 });
    }else if (step === 3) {
        tweenCamera(camera, { x: 0, y: -10, z: 40 },  { x: 1.16, y: -0.12, z: 0.27 });
    }else if (step === 4) {
        tweenCamera(camera, { x: 0, y: -5, z: 70 }, { x: 0, y: 0, z: 0 });
    } else if (step === 5) {
        tweenCamera(camera, { x: 0, y: -1.5, z: 60 }, { x: 0, y: 0, z: 0 });
    }

    camera.updateProjectionMatrix();
    useFrame(state => {
        TWEEN.update();
        // camera.lookAt(camTarget);
    });

    return null;
};

function tweenCamera(camera, cameraPos, target) {
    let pos = camera.position;
    let rot = camera.rotation;
    new TWEEN.Tween(pos)
        .to({ x: cameraPos.x, y: cameraPos.y, z: cameraPos.z }, 1000)
        .easing(TWEEN.Easing.Quadratic.Out)
        .onUpdate(function() {})
        .start();

    new TWEEN.Tween(rot)
        .to({ x: target.x, y: target.y, z: target.z }, 1000)
        .easing(TWEEN.Easing.Quadratic.Out)
        .onUpdate(function() {
            // console.log('tweening camera, xyz: ',pos.x, pos.y, pos.z )
        })
        .start();
}

function UpdateCamera(props) {
    const { step } = props; // step
    let cameraPos = {
        fov: 60,
        near: 0.1,
        far: 1000,
        position: [0, 0, 1]
    };

    return (
        <ChangeCamera step={step} />
    );
}

export default UpdateCamera;
