const RainShader = {
    vertexShader: `
    #define PI 3.14159265359
    uniform float uTime;
    uniform float uHighlight;
    attribute vec3 aSpeed;

    vec4 mod289(vec4 x){
      return x - floor(x * (1.0 / 289.0)) * 289.0;
    }

    vec4 permute(vec4 x){
      return mod289(((x*34.0)+1.0)*x);
    }

    vec4 taylorInvSqrt(vec4 r){
      return 1.79284291400159 - 0.85373472095314 * r;
    }

    vec2 fade(vec2 t) {
      return t*t*t*(t*(t*6.0-15.0)+10.0);
    }

    // Classic Perlin noise
    float cnoise(vec2 P){
      vec4 Pi = floor(P.xyxy) + vec4(0.0, 0.0, 1.0, 1.0);
      vec4 Pf = fract(P.xyxy) - vec4(0.0, 0.0, 1.0, 1.0);
      Pi = mod289(Pi); // To avoid truncation effects in permutation
      vec4 ix = Pi.xzxz;
      vec4 iy = Pi.yyww;
      vec4 fx = Pf.xzxz;
      vec4 fy = Pf.yyww;

      vec4 i = permute(permute(ix) + iy);

      vec4 gx = fract(i * (1.0 / 41.0)) * 2.0 - 1.0 ;
      vec4 gy = abs(gx) - 0.5 ;
      vec4 tx = floor(gx + 0.5);
      gx = gx - tx;

      vec2 g00 = vec2(gx.x,gy.x);
      vec2 g10 = vec2(gx.y,gy.y);
      vec2 g01 = vec2(gx.z,gy.z);
      vec2 g11 = vec2(gx.w,gy.w);

      vec4 norm = taylorInvSqrt(vec4(dot(g00, g00), dot(g01, g01), dot(g10, g10), dot(g11, g11)));
      g00 *= norm.x;
      g01 *= norm.y;
      g10 *= norm.z;
      g11 *= norm.w;

      float n00 = dot(g00, vec2(fx.x, fy.x));
      float n10 = dot(g10, vec2(fx.y, fy.y));
      float n01 = dot(g01, vec2(fx.z, fy.z));
      float n11 = dot(g11, vec2(fx.w, fy.w));

      vec2 fade_xy = fade(Pf.xy);
      vec2 n_x = mix(vec2(n00, n01), vec2(n10, n11), fade_xy.x);
      float n_xy = mix(n_x.x, n_x.y, fade_xy.y);
      return 2.3 * n_xy;
    }

    float map(float value, float oldMin, float oldMax, float newMin, float newMax) {
        return newMin + (newMax - newMin) * (value - oldMin) / (oldMax - oldMin);
    }

    varying float vY;

    vec3 getCurvePosition(float posX, float posZ){
        vec3 newPos = vec3(0.);
        newPos.x = posX;
        newPos.z = posZ;
        float uvX = posX/2400.;
        float uvY = 1.-(posZ)/3200.; 
        vec2 peak = vec2(1.0 - abs(.5 - uvX), 1.0 - abs(.5 - uvY));
        vec2 noise = vec2(
            map(cnoise(vec2(0.3 * uTime + uvX * 5., uvY * 5.)), 0., 1., -2., (peak.x * peak.y * 300.)),
            map(cnoise(vec2(-0.3 * uTime + uvX * 5., uvY * 5.)), 0., 1., -2., 25.)
        );
        newPos.y -= ( (aSpeed.x) * sin(aSpeed.x * uTime* 0.5)*4.0 );
        vY = newPos.y;
        return newPos;
    }

    void main() {
        float posX = position.x;
        float posZ = position.z;
        vec4 mvPosition = vec4(0.);

        if(uHighlight == 0. || uHighlight== 19.){
          vec3 newPos = getCurvePosition(posX, posZ);
          mvPosition = modelViewMatrix * vec4( newPos, 1.0 );    
        } else {
          vec3 transformed = position.xyz;
          float velocity = -50.0 + aSpeed.x *40.0;
          float cycle = mod(uTime, 5.0);
          if(aSpeed.z <= uHighlight){
            transformed.y += (velocity * cycle);
          } else {
            transformed.y = -400.0;
          }
          mvPosition = modelViewMatrix * vec4( transformed, 1.0 );
        }

        gl_Position = projectionMatrix * mvPosition;
        gl_PointSize = 0.5 * ( 400.0 / -mvPosition.z );
    }
  `,
    fragmentShader: `
    uniform sampler2D pointTexture;
    void main() {
        vec3 color = vec3(1.0,1.0,1.0);
        gl_FragColor = vec4(color, 0.8);
        gl_FragColor = gl_FragColor * texture2D( pointTexture, gl_PointCoord );
    }
  `
};



